import React from "react";
import { Link } from "react-router-dom";
// reactstrap components
import {
  Collapse,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  NavbarBrand,
  Navbar,
  NavItem,
  Nav,
  Container,
  Row,
  Col,
} from "reactstrap";
import { basePath } from "../../configs";
import { connect } from "react-redux";
class Sidebar extends React.Component {
  //
  state = {
    collapseOpen: false,
    navItems: [
      // { icon: "ni ni-planet text-blue", name: "Dashboard", link: "/dashboard" },
      { icon: "ni ni-single-02 text-danger", name: "Users", link: "/users" },
      {
        icon: "ni ni-bullet-list-67 text-orange",
        name: "Categories",
        link: "/categories",
      },
      { type: "heading", heading: "Web CMS" },
      {
        icon: "ni ni-money-coins text-pink",
        name: "Web Login Page",
        link: "/cms/web-login-cms",
      },
      {
        icon: "ni ni-paper-diploma text-yellow",
        name: "Web Feed Page",
        link: "/cms/web-feed-cms",
      },
      { type: "heading", heading: "App CMS" }, //
      {
        icon: "ni ni-collection text-success",
        name: "App Login Screen",
        link: "/cms/app-login-cms",
      },
    ],
  };

  activeRoute = (routeName) => {
    return this.props.location.pathname.indexOf(basePath + routeName) > -1
      ? "active"
      : "";
  };

  toggleCollapse = () => {
    this.setState({
      collapseOpen: !this.state.collapseOpen,
    });
  };

  closeCollapse = () => {
    this.setState({
      collapseOpen: false,
    });
  };

  createNavItem = () => {
    const { navItems } = this.state;
    return navItems.map((item, key) => {
      if (item.type === "heading") {
        return (
          <h4 className="ml-4" key={item.heading}>
            {item.heading}
          </h4>
        );
      }
      const active = this.activeRoute(item.link);
      return (
        <NavItem key={key}>
          <Link
            to={basePath + item.link}
            // onClick={this.closeCollapse}
            className={"nav-link " + active}
          >
            <i className={item.icon} />
            {item.name}
          </Link>
        </NavItem>
      );
    });
  };
  render() {
    const { logo } = this.props;
    const { adminUser } = this.props;
    return (
      <Navbar
        className="navbar-vertical fixed-left navbar-light bg-white"
        expand="md"
        id="sidenav-main"
      >
        <Container fluid>
          {/* Toggler */}
          <button
            className="navbar-toggler"
            type="button"
            onClick={this.toggleCollapse}
          >
            <span className="navbar-toggler-icon" />
          </button>
          {/* Brand */}

          <NavbarBrand className="pt-0">
            <img
              className="navbar-brand-img"
              src={require("../../assets/img/logo.png")}
            />
          </NavbarBrand>

          {/* User */}
          <Nav className="align-items-center d-md-none">
            <UncontrolledDropdown nav>
              <DropdownToggle nav>
                <Media className="align-items-center">
                  <span className="avatar avatar-sm rounded-circle">
                    <img alt="..." src={adminUser.image} />
                  </span>
                </Media>
              </DropdownToggle>
              <DropdownMenu className="dropdown-menu-arrow" right>
                <DropdownItem
                  href="#"
                  onClick={(e) => {
                    e.preventDefault();
                    localStorage.removeItem("adminAuthToken");
                    localStorage.removeItem("adminUser");
                    window.location.href = "/";
                  }}
                >
                  <i className="ni ni-user-run" />
                  <span>Logout</span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
          {/* Collapse */}
          <Collapse navbar isOpen={this.state.collapseOpen}>
            {/* Collapse header */}
            <div className="navbar-collapse-header d-md-none">
              <Row>
                {logo ? (
                  <Col className="collapse-brand" xs="6">
                    {logo.innerLink ? (
                      <Link to={logo.innerLink}>
                        <img src={require("../../assets/img/logo.png")} />
                      </Link>
                    ) : (
                      <a href={logo.outterLink}>
                        <img src={require("../../assets/img/logo.png")} />
                      </a>
                    )}
                  </Col>
                ) : null}
                <Col className="collapse-close" xs="6">
                  <button
                    className="navbar-toggler"
                    type="button"
                    onClick={this.toggleCollapse}
                  >
                    <span />
                    <span />
                  </button>
                </Col>
              </Row>
            </div>
            {/* Navigation */}
            <Nav navbar>{this.createNavItem()}</Nav>
          </Collapse>
        </Container>
      </Navbar>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    adminUser: state.auth.adminUser,
  };
};
export default connect(mapStateToProps)(Sidebar);
